.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    font-weight: 500;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.dialogs a {
    text-decoration: none;
}

.dialogsItem {
    width: 200px;
    margin-right: 10px;
}

.dialog {
    padding: 10px 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.dialog:hover {
    background-color: lightblue;
    border-radius: 3px;
}

.dialogsItem .active {
    color: #0404cb;
}

.userAvatar{
    margin-right: 5px;
    border: 2px solid grey!important;
    width: 40px!important;
    height: 40px!important;
}

.messages {
    width: 300px;
    margin-top: 10px;
}

.textarea {
    box-sizing: border-box;
    width: 350px;
    resize: vertical;
    padding: 12px;
    border-radius: 15px;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.06);
    height: 60px;
    resize: none;
    margin-bottom: 10px;
    border: 2px solid black;
    font-size: 15px;
    font-weight: 550;
    line-height: 30px;
}

.button {
    appearance: none;
    -webkit-appearance: none;
    padding: 10px;
    border: none;
    background-color: #0404cb;
    color: #fff;
    font-weight: 600;
    border-radius: 5px;
    width: 30%;
    cursor: pointer;
    opacity: 0.8;
}

.button:focus {
    color: white;
    -webkit-box-shadow: 4px 4px 8px 0px rgba(2, 9, 15, 1);
    -moz-box-shadow: 4px 4px 8px 0px rgba(2, 9, 15, 1);
    box-shadow: 4px 4px 8px 0px rgba(2, 9, 15, 1);
    opacity: 100%;
}



